@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap&family=Fira+Code&display=swap&family=Fira+Mono&display=swap');

.project-preview{
    height: 400px;
    margin-top: 65px;
    margin-bottom: 65px;
    width: 21%;
    background: linear-gradient(0deg, rgba(55, 0, 103, 0.5) 0%, #09143B 100%);
    border-radius: 3px;

    box-shadow: 5px 5px 20px 5px #3d3b3b88;
    backdrop-filter: blur(7px);
    -webkit-backdrop-filter: blur(7px);
    overflow: hidden;
    transition: .5s all;

    display: flex;
    flex-direction: column;

    font-size: 18px;
    font-family: 'SF Mono', 'Fira Code', 'Fira Mono', 'Roboto Mono', monospace;
    color: #ccd6f6;

    ::before{
        position: fixed;
        content: "";
        top: -10%;
        left: -100%;
        transform: rotate(-45deg);
        transition: .7s all;
      }
      &:hover{
        transform: scale(1.1);
        filter: brightness(1.1);
        ::before{
          filter: brightness(.5);
          top: -100%;
          left: 200%;
        }
      }

}

