.project-image{
    height: 35%;
    margin:3%;
    margin-top: 5%;
    object-fit: cover;
    border-radius: 3px;
}

.about-image{
    height: 50%;
    margin-left: 7%;
    margin-right: 7%;
    margin-top: 4%;
    object-fit: cover;
    border-radius: 3px;

}

.project-text-container{
    height: auto;
}

.project-text-container hr{
    margin-top: auto; /* Pushes the hr to the bottom */
    width: 80%;
    border: none;
    border-bottom: 1px solid #ada9a955;
}

.project-text-container h2{
    font-size: 16px;
    text-transform: uppercase;
    padding-left: 5%;
    color: #37FF8B;
}

.project-text-container .card-description{
    font-size: 12px;
    color: #a89ec9;
    padding-left: 5%;
}

.card-info{
    display: flex;
    align-items: center;
    
}

.card-info .card-date{
    width : 50%;
    height: 100%;
    padding-top: 1px;
    color: #a89ec9;
    text-decoration: none;
    .card-date-description{
        font-size: 14px;
    }
}

.card-icons{
    width : 50%;
    display: flex;
    justify-content: center;
    .card-image-icon{
        margin-left: 3px;
        margin-right: 3px;
        height: 25px;
        width: 25px;
    }
}
